@import "../../global.scss";

.portfolio {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 7rem;
  min-height: 90vh;

  h1 {
    font-size: 50px;

    @include mobile {
      font-size: 20px;
    }
  }

  .linkContainer {
    display: flex;
    align-items: center;
    margin-top: 10px;

    @include mobile {
      display: none;
    }

    .icon {
      font-size: 18px;
      margin-right: 5px;
    }

    span {
      font-size: 15px;
      font-weight: 500;
    }

    a {
      font-size: 15px;
      font-weight: 500;
    }
  }

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;

    @include mobile {
      margin: 10px 0;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  .container {
    width: 70%;
    display: flex;
    align-items: start;
    justify-content: start;
    flex-wrap: wrap;

    @include mobile {
      width: 100%;
    }

    .item {
      width: 220px;
      height: 150px;
      border-radius: 20px;
      border: 1px solid rgb(240, 239, 239);
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all 0.5s ease;
      cursor: pointer;
      text-align: center;

      @include mobile {
        width: 130px;
        height: 100px;
      }

      h3 {
        position: absolute;
        font-size: 20px;
        text-align: center;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        text-align: center;
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
